import React from "react";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { RouterLink, useParams, useLocation, Redirect } from "react-router-dom";
import { apiGet, apiPost } from "../../../api";

const ingredientes_test = {
  Harina: {
    quantity: 1,
    weight: 0.5
  },
  Sal: {
    quantity: 1,
    weight: 0.5
  },
  AzÃºcar: {
    quantity: 1,
    weight: 0.5
  }
};

function getIngredients(variables) {
  //console.log(Object.keys(variables));
  return Object.keys(variables);
}

var nombre = getIngredients(ingredientes_test)[0];
var peso = 1;
var cantidad = 1;

var lista_ingredientes = {};
var ingredients = {};

export default function CreateGood(props) {
  let history = useHistory();
  const handleSubmitForm = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    // eslint-disable-next-line no-console
    console.log({
      name: data.get("nombre"),
      weight: data.get("peso"),
      price: data.get("precio"),
      cost: data.get("costo"),
      quantity: data.get("cantidad"),
      barcode: data.get("barcode")
    });
    console.log("Enviando a Back", props.id_zona);
    apiPost(
      "goods",

      JSON.stringify({
        name: data.get("nombre"),
        weight: data.get("peso"),
        price: data.get("precio"),
        cost: data.get("costo"),
        quantity: data.get("cantidad")
      })
    );

    window.location.reload(false);
    //history.push("/Inventary", { id: props.id_zona });
    console.log("los pits", props);
  };

  const [data, setData] = React.useState("Codigo de barra no detectado");
  let [nombre, setnombre] = React.useState("");
  let [peso, setpeso] = React.useState("");
  let [cantidad, setcantidad] = React.useState("");
  let [costo, setcosto] = React.useState("");
  let [precio, setprecio] = React.useState("");
  const [lista_ingredientess, setlista] = React.useState({});
  const [codigoFinal, setCodigoFinal] = React.useState("");

  const ClickIngredient = (event) => {
    //console.log("Clickeado");

    listaChange(event);
  };

  const codigoFinalChange = (event) => {
    if (codigoFinal == "") {
      setCodigoFinal(event);
    }
  };

  const listaChange = (event) => {
    var values = event.target.value.split(",");
    //console.log("values", values);
    let nombre_ingrediente = values[0];
    let nuevo = { quantity: values[1], weight: values[2] };

    lista_ingredientes[nombre_ingrediente] = nuevo;
    setlista(...[values]);

    //console.log(values, "values");

    ingredients[values[0]] = {
      quantity: values[1],
      weight: values[2]
    };

    //console.log(ingredients["Harina"]["weight"], "a");

    //console.log(ingredients, "ingredients");
  };

  const nameChange = (event) => {
    setnombre(event.target.value);
    nombre = event.target.value;
    //console.log(nombre);
  };

  const pesoChange = (event) => {
    setpeso(event.target.value);
    peso = event.target.value;
    //console.log(peso);
  };

  const cantidadChange = (event) => {
    setcantidad(event.target.value);
    cantidad = event.target.value;
    //console.log(cantidad);
  };
  const precioChange = (event) => {
    setprecio(event.target.value);
    precio = event.target.value;
    //console.log(cantidad);
  };
  const costoChange = (event) => {
    setcosto(event.target.value);
    costo = event.target.value;
    //console.log(cantidad);
  };

  return (
    <div>
      <form onSubmit={handleSubmitForm}>
        <section class="section">
          <div class="control">
            <label class="label">Nombre del Producto</label>
            <input
              required
              name="nombre"
              class="input"
              type="text"
              placeholder="Nombre"
              onChange={nameChange}
            />

            <br />
            <br />
            <div class="columns is-mobile">
              <div class="column is-two-quarter ">
                <label class="label">
                  Peso en Kg o unidades (Ej: En un paquete de harina viene un
                  1kg de harina. En una bandeja de huevos, vienen 30 huevos.)
                </label>

                <input
                  class="input "
                  type="number"
                  step=".01"
                  placeholder="1"
                  defaultValue="0"
                  name="peso"
                  onChange={pesoChange}
                />
              </div>
              <div class="column">
                <label class="label">Precio</label>
                <br />
                <br />
                <input
                  class="input "
                  type="number"
                  step=".01"
                  min="0"
                  placeholder="0"
                  defaultValue="0"
                  name="precio"
                  onChange={precioChange}
                />
              </div>
            </div>
          </div>
          <div class="columns is-mobile">
            <div class="column is-two-quarter ">
              <label class="label">
                Costo (Ej: Costo por el kilo o las 30 unidades)
              </label>

              <input
                class="input "
                type="number"
                step=".01"
                min="0"
                placeholder="1500"
                defaultValue="0"
                name="costo"
                onChange={costoChange}
              />
            </div>
            <div class="column">
              <label class="label">
                Cantidad (Ej: Cuantas bandejas o paquetes de harina hay en
                inventario.)
              </label>
              <input
                class="input "
                type="number"
                step=".01"
                min="0"
                placeholder="0"
                defaultValue="0"
                onChange={cantidadChange}
                name="cantidad"
              />
            </div>
          </div>

          <input
            class="input is-disabled"
            type="text"
            defaultValue="0"
            name="barcode"
            value={data}
            disabled
          />
          <br />
          <br />
          {/* THIS ONE IS A VALID COMMENT 
          <BarcodeScannerComponent
            width={500}
            height={500}
            onUpdate={(err, result) => {
              if (result) codigoFinalChange(result.text);
              else setData("Codigo de barra no encontrado");
            }}
          */}
          <button class=" button is-primary " type="submit">
            Submit
          </button>
        </section>
      </form>
    </div>
  );
}
