import React from "react";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { RouterLink, useParams, useLocation, Redirect } from "react-router-dom";
import { apiGet, apiPost } from "../../../api";

const ingredientes_test = {
  Harina: {
    quantity: 1,
    weight: 0.5
  },
  Sal: {
    quantity: 1,
    weight: 0.5
  },
  AzÃºcar: {
    quantity: 1,
    weight: 0.5
  }
};
function get_uid_from_ingrediente(name, original_data) {
  //console.log("original_data", original_data);
  let salida = "";
  Object.keys(original_data).forEach((uid) => {
    if (original_data[uid].name === name) {
      salida = uid;
    }
  });
  return salida;
}

function getIngredients(variables) {
  //console.log(Object.keys(variables));

  return Object.keys(variables);
}
let ingredientessss = [];
var nombre = getIngredients(ingredientes_test)[0];
var peso = 1;
var cantidad = 1;
var lista_ingredientes = {};
var ingredients = {};

let MINUTE_MS = 1;
export default function CreateRecipes(props) {
  let history = useHistory();
  const handleSubmitForm = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    apiPost(
      "area/" + props.id_zona + "/add_recipe",
      JSON.stringify({
        name: data.get("receta"),
        description: data.get("descripcion"),
        author: data.get("autor"),
        ingredients: ingredients
      })
    );

    //window.location.reload(false);
    history.push("/ZoneDashboard", { id: props.id_zona });
    //console.log("los pits", props);
  };

  const [nombree, setnombre] = React.useState("");
  const [pesoo, setpeso] = React.useState("");
  const [cantidadd, setcantidad] = React.useState("");
  const [lista_ingredientess, setlista] = React.useState({});
  const [
    listaIngredientesMostrar,
    setListaIngredientesMostrar
  ] = React.useState([]);

  //getRecipes();
  useEffect(() => {
    MINUTE_MS = 1000;
    const interval = setInterval(() => {
      get_all_ingredients();
    }, MINUTE_MS);
    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  });

  async function get_all_ingredients() {
    ingredientessss = await apiGet("goods");
    let salida = [];
    Object.keys(ingredientessss).map((element) =>
      salida.push(ingredientessss[element].name)
    );
    //console.log("salida", ingredientessss);
    setListaIngredientesMostrar(salida);
  }

  const ClickIngredient = (event) => {
    //console.log("Clickeado");

    listaChange(event);
  };

  const listaChange = (event) => {
    var values = event.target.value.split(",");
    //console.log("values", values);
    let nombre_ingrediente = values[0];
    let uuid = get_uid_from_ingrediente(nombre_ingrediente, ingredientessss);
    //console.log("UUUUUID", uuid);
    let nuevo = {
      quantity: values[1],
      weight: values[2],
      uid: uuid
    };

    lista_ingredientes[nombre_ingrediente] = nuevo;
    setlista(...[values]);

    //console.log(values, "values");

    ingredients[values[0]] = {
      quantity: values[1],
      weight: values[2],
      uid: uuid
    };
  };

  const nameChange = (event) => {
    setnombre(event.target.value);
    nombre = event.target.value;
    //console.log(nombre);
  };

  const DeleteIngredient = (event) => {
    //lista_ingredientes.removeItem(event.target.value);
    //console.log("lista ingredientes", lista_ingredientes);
    //console.log("llave", event.target.value);
    delete lista_ingredientes[event.target.value];
    //console.log("lista ingredientes", lista_ingredientes);
    //lista_ingredientes = Object.omit(lista_ingredientes, event.target.value);
    setnombre(event.target.value);
  };

  const weightChange = (event) => {
    setpeso(event.target.value);
    peso = event.target.value;
    //console.log(peso);
  };

  const cantidadChange = (event) => {
    setcantidad(event.target.value);
    cantidad = event.target.value;
    //console.log(cantidad);
  };

  return (
    <div>
      <form onSubmit={handleSubmitForm}>
        <section class="section">
          <div class="control">
            <input
              required
              name="receta"
              class="input"
              type="text"
              placeholder="Nombre de la receta."
            />
            <br />
            <br />

            <input
              required
              name="descripcion"
              class="input "
              type="text"
              placeholder="Descripcion"
            />
            <br />
            <br />
            <input
              required
              name="autor"
              class="input "
              type="text"
              placeholder="Autor"
            />
            <br />
            <br />
          </div>
          <div class="columns is-mobile">
            <div class="column is-two-quarter ">
              <label class="label">Ingrediente</label>
              <div class="select is-fullwidth ">
                <select name="nombre" value={nombre} onChange={nameChange}>
                  {listaIngredientesMostrar.map((row) => (
                    <option>{row}</option>
                  ))}
                </select>
              </div>
            </div>
            <div class="column">
              <label class="label">Cantidad</label>
              <input
                class="input "
                type="number"
                placeholder="1"
                defaultValue="1"
                onChange={cantidadChange}
              />
            </div>
            <div class="column">
              <label class="label">Peso o unidades</label>
              <input
                class="input "
                type="number"
                step=".01"
                placeholder="1"
                defaultValue="1"
                onChange={weightChange}
              />
            </div>
            <div class="column">
              <label class="label">Agregar</label>
              <button
                class="button is-link"
                value={[nombre, cantidad, peso]}
                onClick={ClickIngredient}
                type="button"
              >
                +
              </button>
            </div>
          </div>
          <section class="section">
            <h1 class="title">Ingredientes</h1>

            <ul>
              {getIngredients(lista_ingredientes).map((row) => (
                <div class="columns is-mobile">
                  <div class="column is-two-quarter">
                    <li>
                      {row} - Cantidad: {lista_ingredientes[row]["quantity"]} -{" "}
                      Peso:
                      {lista_ingredientes[row]["weight"]} Kg
                    </li>
                  </div>

                  <div class="column">
                    <button
                      class="button is-link"
                      value={row}
                      onClick={DeleteIngredient}
                      type="button"
                    >
                      x
                    </button>
                  </div>
                </div>
              ))}
            </ul>
          </section>
          <button class=" button is-primary " type="submit">
            Submit
          </button>
        </section>
      </form>
    </div>
  );
}
