export default function EditGood(props) {
  const { handle } = useParams();
  const location = useLocation();

  let history = useHistory();
  const handleSubmitForm = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    // eslint-disable-next-line no-console
    /*console.log({
      name: data.get("nombre"),
      weight: data.get("peso"),
      price: data.get("precio"),
      cost: data.get("costo"),
      quantity: data.get("cantidad"),
      barcode: data.get("barcode")
    });*/
    //console.log("Enviando a Back", cosa.uid);
    apiPut(
      "goods/" + cosa.uid,
      JSON.stringify({
        name: data.get("nombre"),
        weight: data.get("peso"),
        price: data.get("precio"),
        cost: data.get("costo"),
        quantity: data.get("cantidad")
      })
    );

    history.push("/Inventary");
  };

  let [nombre, setnombre] = React.useState("");
  let [peso, setpeso] = React.useState("");
  let [cantidad, setcantidad] = React.useState("");
  let [costo, setcosto] = React.useState("");
  let [precio, setprecio] = React.useState("");

  const onloadpage = (event) => {
    setnombre(event.target.value);

    //console.log(cantidad);
  };
  const cosa = location.state;
  //console.log("Cosas recibidas", cosa);
  //console.log("este deberia ser el state", cosa);

  const nameChange = (event) => {
    setnombre(event.target.value);
    nombre = event.target.value;
    //console.log(nombre);
  };

  const pesoChange = (event) => {
    setpeso(event.target.value);
    peso = event.target.value;
    //console.log(peso);
  };

  const cantidadChange = (event) => {
    setcantidad(event.target.value);
    cantidad = event.target.value;
    //console.log(cantidad);
  };
  const precioChange = (event) => {
    setprecio(event.target.value);
    precio = event.target.value;
    //console.log(cantidad);
  };
  const costoChange = (event) => {
    setcosto(event.target.value);
    costo = event.target.value;
    //console.log(cantidad);
  };

  return (
    <div>
      <DefaultNav />
      <form onSubmit={handleSubmitForm}>
        <section class="section">
          <div class="control">
            <label class="label">Nombre del Producto</label>
            <input
              required
              name="nombre"
              class="input"
              type="text"
              placeholder="Nombre"
              defaultValue={cosa.name || ""}
              onChange={nameChange}
            />

            <br />
            <br />
            <div class="columns is-mobile">
              <div class="column is-two-quarter ">
                <label class="label">Peso en Kg (Opcional)</label>

                <input
                  class="input "
                  type="number"
                  step=".01"
                  min="0"
                  placeholder="1500"
                  defaultValue={cosa.weight || ""}
                  name="peso"
                  onChange={pesoChange}
                />
              </div>
              <div class="column">
                <label class="label">Precio</label>
                <input
                  class="input "
                  type="number"
                  step=".01"
                  min="0"
                  placeholder="0"
                  defaultValue={cosa.price || ""}
                  name="precio"
                  onChange={precioChange}
                />
              </div>
            </div>
          </div>
          <div class="columns is-mobile">
            <div class="column is-two-quarter ">
              <label class="label">Costo</label>

              <input
                class="input "
                type="number"
                step=".01"
                min="0"
                placeholder="1500"
                name="costo"
                onChange={costoChange}
                defaultValue={cosa.cost || ""}
              />
            </div>
            <div class="column">
              <label class="label">Cantidad</label>
              <input
                class="input "
                type="number"
                step=".01"
                min="0"
                placeholder="0"
                defaultValue={cosa.quantity || ""}
                onChange={cantidadChange}
                name="cantidad"
              />
            </div>
          </div>

          <br />
          <br />
          {/* THIS ONE IS A VALID COMMENT 
          <BarcodeScannerComponent
            width={500}
            height={500}
            onUpdate={(err, result) => {
              if (result) codigoFinalChange(result.text);
              else setData("Codigo de barra no encontrado");
            }}
          */}
          <button class=" button is-primary " type="submit">
            Submit
          </button>
        </section>
      </form>
    </div>
  );
}
