import React from "react";
import { RouterLink, Route, Link } from "react-router-dom";
import { apiDelete } from "../../api";

export default function ZoneCard(props) {
  const id = props.id;
  const name = props.name;
  const description = props.description;

  function DeleteZone() {
    apiDelete("area/" + id, JSON.stringify({}));
  }
  return (
    <div>
      <section className="section">
        <div class="card">
          <header class="card-header">
            <p class="card-header-title">{name}</p>
            <button
              onClick={DeleteZone}
              class="button is-danger"
              aria-label="more options"
            >
              X
            </button>
          </header>
          <div class="card-content">
            <div class="content">
              <label class="label">Descripcion:</label>
              {description}
              <br />
            </div>
          </div>
          <footer class="card-footer">
            <Link
              class="card-footer-item"
              to={{ pathname: "/Recipes", state: { id: id } }}
            >
              Recetas
            </Link>
            <Link
              class="card-footer-item"
              to={{ pathname: "/Registry", state: { id: id } }}
            >
              Registro
            </Link>
            <Link
              class="card-footer-item"
              to={{ pathname: "/History", state: { id: id } }}
            >
              Historial
            </Link>
          </footer>
        </div>
      </section>

      <span class="icon">
        <i class="fas fa-home"></i>
      </span>
    </div>
  );
}
