import React from "react";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import DefaultNav from "../../../DefaultNav/DefaultNav";
import { apiGet, apiPut } from "../../../api";

const ingredientes_test = {
  Harina: {
    quantity: 1,
    weight: 0.5
  },
  Sal: {
    quantity: 1,
    weight: 0.5
  },
  AzÃºcar: {
    quantity: 1,
    weight: 0.5
  }
};
let ingredientessss = [];
function getIngredients(variables) {
  //console.log(Object.keys(variables));
  return Object.keys(variables);
}
function get_uid_from_ingrediente(name, original_data) {
  //console.log("original_data", original_data);
  let salida = "";
  Object.keys(original_data).forEach((uid) => {
    if (original_data[uid].name === name) {
      salida = uid;
    }
  });
  return salida;
}

var nombre = getIngredients(ingredientes_test)[0];
var peso = 1;
var cantidad = 1;
var lista_ingredientes = {};
var ingredients = {};
let MINUTE_MS = 1000;
export default function EditRecipe(props) {
  const { handle } = useParams();
  const location = useLocation();

  let history = useHistory();
  const handleSubmitForm = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    // eslint-disable-next-line no-console
    console.log({
      receta: data.get("receta"),
      descripcion: data.get("descripcion"),
      autor: data.get("autor")
    });
    //console.log("Enviando a Back", props.id_zona);
    apiPut(
      "area/" + cosa.id_zona + "/update_recipe/" + cosa.row.uid,
      JSON.stringify({
        name: data.get("receta"),
        description: data.get("descripcion"),
        author: data.get("autor"),
        price: data.get("price"),
        ingredients: ingredients
      })
    );

    history.push("/Recipes", { id: cosa.id_zona });
  };

  const [nombree, setnombre] = React.useState("");
  const [pesoo, setpeso] = React.useState("");
  const [cantidadd, setcantidad] = React.useState("");
  const [price, setprice] = React.useState("");
  const [lista_ingredientess, setlista] = React.useState({});
  const [
    listaIngredientesMostrar,
    setListaIngredientesMostrar
  ] = React.useState([]);

  async function get_all_ingredients() {
    ingredientessss = await apiGet("goods");
    let salida = [];
    Object.keys(ingredientessss).map((element) =>
      salida.push(ingredientessss[element].name)
    );
    setListaIngredientesMostrar(salida);
    //console.log(salida);
  }
  useEffect(() => {
    MINUTE_MS = 1000;
    const interval = setInterval(() => {
      get_all_ingredients();
      //console.log("Logs every minute");
    }, MINUTE_MS);
    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  });

  const ClickIngredient = (event) => {
    //console.log("Clickeado");

    listaChange(event);
  };

  const listaChange = (event) => {
    var values = event.target.value.split(",");
    //console.log("values", values);
    let nombre_ingrediente = values[0];
    let nuevo = { quantity: values[1], weight: values[2] };
    let uuid = get_uid_from_ingrediente(nombre_ingrediente, ingredientessss);

    lista_ingredientes[nombre_ingrediente] = nuevo;
    setlista(...[values]);

    //console.log(values, "values");

    ingredients[values[0]] = {
      quantity: values[1],
      weight: values[2],
      uid: uuid
    };

    //console.log(ingredients["Harina"]["weight"], "a");

    //console.log(ingredients, "ingredients");
  };

  const nameChange = (event) => {
    setnombre(event.target.value);
    nombre = event.target.value;
    //console.log(nombre);
  };
  const priceChange = (event) => {
    setprice(event.target.value);
    price = event.target.value;
    //console.log(nombre);
  };

  const DeleteIngredient = (event) => {
    //lista_ingredientes.removeItem(event.target.value);
    //console.log("lista ingredientes", lista_ingredientes);
    //console.log("llave", event.target.value);
    delete lista_ingredientes[event.target.value];
    //console.log("lista ingredientes", lista_ingredientes);
    //lista_ingredientes = Object.omit(lista_ingredientes, event.target.value);
    setnombre(event.target.value);
  };

  const weightChange = (event) => {
    setpeso(event.target.value);
    peso = event.target.value;
    //console.log(peso);
  };

  const cantidadChange = (event) => {
    setcantidad(event.target.value);
    cantidad = event.target.value;
    //console.log(cantidad);
  };

  const onloadpage = (event) => {
    setnombre(event.target.value);

    //console.log(cantidad);
  };
  const cosa = location.state;
  lista_ingredientes = cosa.row.ingredients;
  ingredients = cosa.row.ingredients;
  //console.log("este deberia ser el state", cosa);

  //  // constructor(props) {
  //     super(props);
  //     this.handleChange = this.handleChange.bind(this);

  //     this.state = {
  //       ingredient_name: "",
  //       ingredient_weight: "",
  //       ingredient_quantity: ""
  //     };
  //   }
  //   handleChange(event) {
  //     this.setState({ ingredient_name: event.target.ingredient_name });
  //     console.log(this.state.ingredient_name);
  //   }

  return (
    <div>
      <DefaultNav />
      <form onSubmit={handleSubmitForm}>
        <section class="section">
          <div class="control" onLoad={onloadpage} value="asd">
            <input
              required
              name="receta"
              class="input"
              type="text"
              placeholder={"Nombre de la receta"}
              defaultValue={cosa.row.name || ""}
            />
            <br />
            <br />

            <input
              required
              name="descripcion"
              class="input "
              type="text"
              placeholder="Descripcion"
              defaultValue={cosa.row.description || ""}
            />
            <br />
            <br />
            <input
              required
              name="autor"
              class="input "
              type="text"
              placeholder="Autor"
              defaultValue={cosa.row.author || ""}
            />
            <br />
            <br />
            <label class="label">Precio</label>
            <input
              required
              name="price"
              class="input "
              type="number"
              step=".01"
              placeholder="Precio final"
              defaultValue={cosa.row.price || ""}
            />
            <br />
            <br />
          </div>
          <div class="columns is-mobile">
            <div class="column is-two-quarter ">
              <label class="label">Ingrediente</label>
              <div class="select is-fullwidth ">
                <select name="nombre" value={nombre} onChange={nameChange}>
                  {listaIngredientesMostrar.map((row) => (
                    <option>{row}</option>
                  ))}
                </select>
              </div>
            </div>
            <div class="column">
              <label class="label">Cantidad</label>
              <input
                class="input "
                type="number"
                placeholder="1"
                defaultValue="1"
                onChange={cantidadChange}
              />
            </div>
            <div class="column">
              <label class="label">Peso</label>
              <input
                class="input "
                type="number"
                step=".01"
                placeholder="1"
                defaultValue="1"
                onChange={weightChange}
              />
            </div>
            <div class="column">
              <label class="label">Agregar</label>
              <button
                class="button is-link"
                value={[nombre, cantidad, peso]}
                onClick={ClickIngredient}
                type="button"
              >
                +
              </button>
            </div>
          </div>
          <section class="section">
            <h1 class="title">Ingredientes</h1>

            <ul>
              {getIngredients(lista_ingredientes).map((row) => (
                <div class="columns is-mobile">
                  <div class="column is-two-quarter">
                    <li>
                      {row} - Cantidad: {lista_ingredientes[row]["quantity"]} -{" "}
                      Peso:
                      {lista_ingredientes[row]["weight"]} Kg
                    </li>
                  </div>

                  <div class="column">
                    <button
                      class="button is-link"
                      value={row}
                      onClick={DeleteIngredient}
                      type="button"
                    >
                      x
                    </button>
                  </div>
                </div>
              ))}
            </ul>
          </section>
          <button class=" button is-primary " type="submit">
            Submit
          </button>
        </section>
      </form>
    </div>
  );
}
