import React from "react";
import { useState, useEffect } from "react";
import DefaultNav from "../../DefaultNav/DefaultNav";
import { Redirect, useHistory } from "react-router-dom";
import ZoneCard from "./ZoneCard";
import CreateZone from "./CreateZone";
import { apiGet } from "../../api";

let test_variables = [
  {
    id: "123",
    name: "Panaderia",
    description: "Zona manejada por trabajador 1"
  },
  {
    id: "789",
    name: "Pasteleria",
    description: "Zona manejada por trabajador N"
  }
];
let MINUTE_MS = 0;
export default function ZoneDashboard(props) {
  const [zoness, setZones] = React.useState([]);

  // let salida = apiGet("area");
  // console.log(salida);
  //getZones();
  MINUTE_MS = 1000;

  useEffect(() => {
    const interval = setInterval(() => {
      getZones();
      console.log("Logs every minute");
      test_variables = zoness;
    }, MINUTE_MS);

    //console.log("zones", zoness);
    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  }, []);

  async function getZones() {
    let registries = await apiGet("area");
    //console.log("registries", registries);
    setZones(registries);
    //console.log("test_variables", test_variables);
  }
  test_variables = zoness;

  return <ZoneDash {...{ cosas: zoness }} />;
}

class ZoneDash extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      class_1: "is-active",
      style_1: "block",
      class_2: "",
      style_2: "none"
    };
  }

  clickList = () => {
    this.setState({
      class_1: "is-active",
      class_2: "",
      style_1: "block",
      style_2: "none"
    });
  };

  clickAddZone = () => {
    this.setState({
      class_1: "",
      class_2: "is-active",
      style_1: "none",
      style_2: "block"
    });
  };
  render() {
    return (
      <div>
        <DefaultNav />
        <div class="tabs is-toggle is-fullwidth is-large">
          <ul class="is-mobile">
            <li class={this.state.class_1}>
              <a onClick={this.clickList}>
                <span class="icon">
                  <i class="fas fa-image" aria-hidden="true"></i>
                </span>
                <span>Zonas</span>
                <div
                  style={{ display: this.state.class_1 ? "is-active" : "" }}
                ></div>
              </a>
            </li>
            <li class={this.state.class_2}>
              <a onClick={this.clickAddZone}>
                <span class="icon">
                  <i class="fas fa-music" aria-hidden="true"></i>
                </span>
                <span>Crear Zona</span>
              </a>
            </li>
          </ul>
        </div>

        <div style={{ display: this.state.class_1 ? "" : "none" }}>
          {this.props.cosas === undefined
            ? alert(this.props.cosas)
            : this.props.cosas.map((row) => <ZoneCard {...row} />)}
        </div>

        <div style={{ display: this.state.class_2 ? "" : "none" }}>
          <CreateZone />
        </div>
      </div>
    );
  }
}
