import React from "react";
import { useState, useEffect } from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import DefaultNav from "../../../DefaultNav/DefaultNav";
import CreateRecipes from "./CreateRecipe";
import SingleRecipe from "./SingleRecipe";
import { useHistory } from "react-router-dom";
import { apiGet } from "../../../api";

let test_variables = [];

let id_zona = 0;
let MINUTE_MS = 0;
export default function Recipe(props) {
  let history = useHistory();

  const { handle } = useParams();
  const location = useLocation();
  if (location.state === undefined) {
    //history.push("/ZoneDashboard");
  }

  id_zona = location.state.id;

  //console.log("Id de la Zona?", id_zona);

  const [recipess, setRecipes] = React.useState([]);

  MINUTE_MS = 1000;
  //getRecipes();
  useEffect(() => {
    const interval = setInterval(() => {
      getRecipes();
      console.log("Logs every minute");
    }, MINUTE_MS);
    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  });

  async function getRecipes() {
    {
      let resultados = await apiGet("area/" + id_zona + "/get_all_recipies");
      console.log("RECIPES", resultados);
      console.log("Lolazo", Object.keys(resultados).includes("message"));
      if (Object.keys(resultados).includes("message") == true) {
        test_variables = undefined;
      } else {
        test_variables = resultados;
        setRecipes(resultados);
      }
    }
  }
  test_variables = recipess;
  console.log("recetas", test_variables);
  return (
    <div>
      <RecipeContainer {...id_zona} />
    </div>
  );
}

export class RecipeContainer extends React.Component {
  constructor(props) {
    super(props);

    //console.log("props", props);
    this.state = {
      class_1: "is-active",
      style_1: "block",
      class_2: "",
      style_2: "none"
    };
  }

  clickList = () => {
    this.setState({
      class_1: "is-active",
      class_2: "",
      style_1: "block",
      style_2: "none"
    });
  };

  clickAddRecipe = () => {
    this.setState({
      class_1: "",
      class_2: "is-active",
      style_1: "none",
      style_2: "block"
    });
  };

  render() {
    return (
      <div>
        <DefaultNav />
        <div class="tabs is-toggle is-fullwidth is-large">
          <ul>
            <li class={this.state.class_1}>
              <a onClick={this.clickList}>
                <span class="icon">
                  <i class="fas fa-image" aria-hidden="true"></i>
                </span>
                <span>Recetas</span>
                <div
                  style={{ display: this.state.class_1 ? "is-active" : "" }}
                ></div>
              </a>
            </li>
            <li class={this.state.class_2}>
              <a onClick={this.clickAddRecipe}>
                <span class="icon">
                  <i class="fas fa-music" aria-hidden="true"></i>
                </span>
                <span>Crear Receta</span>
              </a>
            </li>
          </ul>
        </div>

        <div style={{ display: this.state.class_1 ? "" : "none" }}>
          {test_variables === undefined ? (
            <p>nada por aca</p>
          ) : (
            test_variables.map((row) => (
              <SingleRecipe {...{ row, id_zona: id_zona }} />
            ))
          )}
        </div>

        <div style={{ display: this.state.class_2 ? "" : "none" }}>
          <CreateRecipes {...{ id_zona: id_zona, props: this.state }} />
        </div>
      </div>
    );
  }
}
