import React from "react";
import { useState, useEffect } from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import DefaultNav from "../../../DefaultNav/DefaultNav";
import CreateGood from "./CreateGood";
import SingleGood from "./SingleGood";
import { useHistory } from "react-router-dom";
import { apiGet } from "../../../api";

let test_variables = [];

let id_zona = 0;

export default function Goods(props) {
  let history = useHistory();

  const { handle } = useParams();
  const location = useLocation();
  if (location.state === undefined) {
    //history.push("/ZoneDashboard");
  }

  const [Goodss, setGoods] = React.useState([]);

  const MINUTE_MS = 1000;

  useEffect(() => {
    const interval = setInterval(() => {
      getGoods();
      console.log("Logs every minute");
    }, MINUTE_MS);
    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  });

  async function getGoods() {
    let resultados = await apiGet("goods");
    //console.log(resultados);
    setGoods(resultados);
  }

  test_variables = Goodss;
  //console.log("recetas", test_variables);
  return (
    <div>
      <GoodContainer {...id_zona} />
    </div>
  );
}

export class GoodContainer extends React.Component {
  constructor(props) {
    super(props);

    //console.log("props", props);
    this.state = {
      class_1: "is-active",
      style_1: "block",
      class_2: "",
      style_2: "none"
    };
  }

  clickList = () => {
    this.setState({
      class_1: "is-active",
      class_2: "",
      style_1: "block",
      style_2: "none"
    });
  };

  clickAddRecipe = () => {
    this.setState({
      class_1: "",
      class_2: "is-active",
      style_1: "none",
      style_2: "block"
    });
  };

  render() {
    return (
      <div>
        <DefaultNav />
        <div class="tabs is-toggle is-fullwidth is-large">
          <ul>
            <li class={this.state.class_1}>
              <a onClick={this.clickList}>
                <span class="icon">
                  <i class="fas fa-image" aria-hidden="true"></i>
                </span>
                <span>Insumos</span>
                <div
                  style={{ display: this.state.class_1 ? "is-active" : "" }}
                ></div>
              </a>
            </li>
            <li class={this.state.class_2}>
              <a onClick={this.clickAddRecipe}>
                <span class="icon">
                  <i class="fas fa-music" aria-hidden="true"></i>
                </span>
                <span>Crear Insumo</span>
              </a>
            </li>
          </ul>
        </div>

        <div style={{ display: this.state.class_1 ? "" : "none" }}>
          {Object.keys(test_variables).map((row) => (
            <SingleGood {...test_variables[row]} />
          ))}
        </div>

        <div style={{ display: this.state.class_2 ? "" : "none" }}>
          <CreateGood {...{ props: this.state }} />
        </div>
      </div>
    );
  }
}
