import React from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { Link } from "react-router-dom";
import { apiDelete } from "../../../api";

export default function SingleGood(props) {
  let history = useHistory();

  const DeleteGood = (event) => {
    //console.log(props.uid);
    apiDelete(`goods/${props.uid}`, JSON.stringify({}));

    history.push("/Inventary");
  };

  return (
    <div>
      <section className="section">
        <div class="card">
          <header class="card-header">
            <p class="card-header-title">{props.name}</p>
          </header>
          <div class="card-content">
            <div class="content">
              <div class="columns is-mobile">
                <div class="column is-mobile ">
                  Datos Insumo:
                  <ul>
                    {Object.keys(props).map((row) => (
                      <li>
                        {row}: {props[row]}
                      </li>
                    ))}
                  </ul>
                  <br />
                </div>
                <div class="column is-mobile ">
                  <figure class="image is-256x256">
                    <img src="https://roggendorf.cl/wp-content/uploads/2016/12/Hallullas-min.jpg" />
                  </figure>
                </div>
              </div>
            </div>
          </div>
          <footer class="card-footer">
            <Link
              class="card-footer-item"
              to={{ pathname: "/EditGood", state: props }}
            >
              Editar
            </Link>

            <a onClick={DeleteGood} class="card-footer-item">
              Eliminar
            </a>
          </footer>
        </div>
      </section>

      <span class="icon">
        <i class="fas fa-home"></i>
      </span>
    </div>
  );
}
